import React, { useState } from 'react'
import {auth, createUser, loginUser } from '../Data/Firebase'
import {Container, Title, Input, Button} from './styles'


export default function Login() {
    const [email, setEmail] = useState('')
    const [senha, setSenha] = useState('')

    const handleLogin = () =>{
        loginUser(auth, email, senha).then(res =>{
            alert('Sucesso, login efetuado!', res)
        })
    }
    const handleCadastro = () =>{
        createUser(auth, email, senha).then(res =>{
            alert('Sucesso, conta cadastrada!', res)
        })
    }

  return (
    <Container>
        <Title>Cadastre-se e faça seu login!</Title>
        <Input value={email} onChange={e =>setEmail(e.target.value)} type='text' placeholder='Digite seu Email'/>
        <Input value={senha} onChange={e =>setSenha(e.target.value)} type='password' placeholder='Digite sua Senha'/>
        <Button primary onClick={handleCadastro}>Cadastro</Button>
        <Button onClick={handleLogin}>Login</Button>
    </Container>
  )
}

